import React, {useState, useEffect} from 'react';

import {
    Button,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function EditFieldDialog({active, setActive, field_id, field_name}) {
    const [field, setField] = useState("");

    function onSubmitField(){
        fetch(process.env.REACT_APP_API+'fields/id/'+ field_id +'?format=json',{
            method: 'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "name": field
            }) 
        })
    };

    useEffect(() => {
        setField(field_name);
    }, [field_name])

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>Изменить название месторождения</DialogTitle>
                <Form name="addfield" onSubmit={onSubmitField}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="edit_field"
                            value={field}
                            onChange={e => setField(e.target.value)}
                            />
                        </Form.Group>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}>Сохранить</Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default EditFieldDialog;